.projects {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 80vh;
}

.projects__container{
    background-color: var(--projetos);
    color: black;
    height: 80vh;
}

@media screen and (max-width: 600px) {
    .projects {
        height: auto;
    }

    .projects__container {
        height: auto;
    }
}