.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-image: var(--header);
    color: white;
    padding: 0 4rem;
    height: 56px;
}

.header__title {
    font-size: 24px;
}

.header__navbar {
    display: flex;
    justify-content: center;
    align-items: center;
}

.header__item {
    list-style: none;
    box-sizing: border-box;
    padding-left: 8px;
    font-size: 16px;
}

.header a {
    color: white;
    text-decoration: none;
}

.header a:hover {
    border-bottom: 2px solid white;
}

@media screen and (max-width: 600px) {
    .header__title {
        font-size: 16px;
    }

    .header__item {
        font-size: 16px;
    }
}