.template-grid {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    padding-left: 1rem;
}

.card {
    display: grid;
    grid-template-areas: 
        "card-imagem"
        "card-corpo";
    grid-template-rows: 173px auto;
    background-color: white;
    border: none;
    box-shadow: 2px 12px 24px rgba(58, 41, 41, 0.8);
    width: calc(25% - 1rem);
    margin-right: 1rem;
    border-radius: 16px;
    transform:scale(0.9); 
}

.card__imagem {
    grid-area: card-imagem;
    width: 100%;
    height: 100%;
    border-radius: 16px 16px 0 0;
}

.card__corpo {
    grid-area: card-corpo;
    display: grid;
    grid-template-areas:
        "titulo"
        "descricao"
        "botao";
    grid-template-rows: 36px auto 36px;
}

.card__titulo {
    grid-area: titulo;
    align-items: center;
    justify-content: center;
}

.card__descricao {
    grid-area: descricao;
}

.card__button {
    grid-area: botao;
    border-radius: 8px 8px 16px 16px;
    text-decoration: none;
    background-color: var(--button);
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.card__button:hover {
    background-color: var(--button--hover);
}

.card__button:visited {
    text-decoration: none;
}

.card__button:active {
    text-decoration: none;
}

@media screen and (max-width: 600px) {
    .template-grid {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        padding: 8px 8px;
    }

    .card {
        width: 100%;
        transition: none;
        transform: scale(1);
        margin-bottom: 1rem;
    }

}